/*
  보훈자료
*/
@use "variables" as *;
@use "config" as *;

.search-detail-box {
  margin-bottom: 60px;
}

.archive-search {
  &-visual {
    background-image: url(../images/archive/visual.png);
    background-size: 1280px 300px;
    background-repeat: no-repeat;
    max-width: 1280px;
    margin: 0 auto;
  }

  &-inner {
    max-width: 1280px;
    margin: 0 auto;
    padding-top: 70px;
  }

  &-title {
    font-size: 40px;
    font-weight: bold;
    letter-spacing: -2px;
    text-align: center;
    color: #3e3c62;
    margin-bottom: 6px;
  }

  &-form {
    max-width: 540px;
    margin: 0 auto 50px;

    .search-form {
      position: relative;
      display: flex;
      align-items: center;
      border-radius: 10px;
      background-color: $white;
      box-shadow: 0 3px 10px 0 rgba(36, 98, 214, 0.2);
      border: solid 3px #2462d6;

      &::after {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 120px;
        width: 1px;
        height: 20px;
        background-color: #dfdfdf;
      }

      .form-select {
        flex: 0 0 120px;
        height: 60px;
        line-height: 56px;
        border: 0;
        border-radius: 0;
        background-color: transparent;
        font-size: 20px;
        padding: 0 20px;
      }

      .form-input {
        flex: 1;
        height: 60px;
        line-height: 56px;
        border: 0;
        border-radius: 0;
        background-color: transparent;
        font-size: 20px;
        padding: 0 20px;
      }

      .search-submit {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 60px;
        height: 60px;
      }
    }
  }

  &-feature {
    position: relative;
    max-width: 640px;
    margin: 0 auto;

    .slider-wrapper {
      position: relative;
      max-width: 640px;
      margin: 0 auto;
      box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2);
      border-radius: 5px;
      overflow: hidden;
    }

    .swiper-slide {
      position: relative;
      display: flex;
      align-items: center;
      background-color: #003675;
      padding: 5px;

      .link {
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 1;
        top: 0;
        left: 0;
      }

      .img {
        width: 160px;
        height: 90px;
        border-radius: 2px;
        background-color: #f2e7db;
        overflow: hidden;
        flex-shrink: 0;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
          max-width: 100%;
          max-height: 100%;
          object-fit: cover;
        }
      }

      .info {
        min-width: 0;
        flex: 1;
        word-break: keep-all;
        padding: 4px 20px 0;

        .info-title {
          font-size: 19px;
          line-height: 24px;
          font-weight: bold;
          letter-spacing: -.95px;
          color: $white;
          margin-bottom: 5px;

          &::after {
            content: '';
            display: inline-flex;
            width: 6px;
            height: 9px;
            background-image: url(../images/archive/ico-more-white.png);
            background-size: cover;
            margin-left: 7px;
            transform: translateY(-3px);
          }
        }

        .info-desc {
          font-size: 15px;
          line-height: 20px;
          letter-spacing: -0.75px;
          color: $gray-ccc;
          //display: -webkit-box;
          //overflow: hidden;
          //text-overflow: ellipsis;
          //-webkit-box-orient: vertical;
          //-webkit-line-clamp: 2;
        }
      }
    }

    .btn-nav {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 38px;
      height: 38px;
      border-radius: 100%;
      z-index: 2;

      &.btn-nav-prev {
        left: -19px;
      }

      &.btn-nav-next {
        right: -19px;
      }

      &.swiper-button-disabled {
        display: none;
      }
    }
  }
}

.archive-container {
  .section-header {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .section-filter {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .section-title {
    font-size: 32px;
    font-weight: bold;
    letter-spacing: -1.5px;
  }

  .btn-more {
    display: flex;
    align-items: center;
    gap: 5px;
    justify-content: center;
    padding: 5px;
    border-radius: 6px;
    transition: .2s;
    font-size: 15px;
    letter-spacing: -0.8px;

    &:hover {
      background-color: $bg-hover;
    }

    &:active {
      background-color: $bg-active;
    }
  }

  .result-group-wrapper {
    .result-group {
      .person-list {
        margin-bottom: 60px;
      }

      .history-list {
        margin-bottom: 30px;

        &.history-list-map {
          margin-bottom: 0;
        }
      }

      .photo-list {
        padding-bottom: 40px;
      }

      .book-list {
        padding-bottom: 40px;
      }
    }
  }

  .result-group {
    .person-list {
      grid-template-columns: repeat(6, minmax(0, 1fr));
    }

    .video-list {
      grid-template-columns: repeat(4, minmax(0, 1fr));
      gap: 30px 60px;
    }
  }

  .detail-content {
    .memorial-detail-content {
      display: flex;
      gap: 30px;

      .photo-list {
        grid-template-columns: repeat(4, minmax(0, 1fr));
        gap: 30px 27px;
      }
    }

    .title-area {
      margin-bottom: 20px;

      .title {
        font-size: 24px;
        font-weight: bold;
        margin-bottom: 10px;

        .badge {
          transform: translateY(-2px);
          height: 23px;
          line-height: 21px;
          font-size: 15px;
          padding: 0 8px;
        }
      }
    }

    .desc-area {
      margin-bottom: 17px;
      margin-top: -14px;

      .desc {
        font-size: 17px;
      }
    }

    .photo-slider-wrapper {
      margin-bottom: 42px;
      margin-top: 20px;

      .btn-nav {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 38px;
        height: 38px;
        z-index: 2;

        &.btn-nav-prev {
          left: 54px;
        }

        &.btn-nav-next {
          right: 54px;
        }

        &.swiper-button-disabled {
          display: none;
        }
      }

      .photo-slider {
        position: relative;
        padding: 40px 60px;
        border-radius: 20px;
        border: solid 1px #cadaee;
        background-color: #f6f8f7;

        &.only-child {
          padding: 30px 20px;
        }

        .swiper {
          padding: 10px;
        }

        .swiper-slide {
          .img {
            display: block;
            border-radius: 10px;
            overflow: hidden;
            aspect-ratio: 220 / 165;
            transition: .2s;
            border: 2px solid transparent;

            &:hover {
              box-shadow: 0 2px 10px 0 rgba(37, 110, 244, 0.5);
              border: solid 2px #256ef4;
              background-color: #d6d6d6;
            }
          }

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }

          &:only-child {
            width: 426px !important;
            margin: 0 auto !important;

            .img {
              width: 426px;
            }
          }
        }
      }
    }

    // 기본정보
    .info-list-wrap {
      //width: 340px;
      //flex-shrink: 0;
      //padding: 0 30px;
    }

    .info-list {
      .row {
        display: flex;
        border-top: 1px dashed #e3e7ee;
        padding: 7px 0;

        &:last-child {
          border-bottom: 1px dashed #e3e7ee;
        }

        dt {
          position: relative;
          width: 100px;
          padding-left: 20px;
          font-size: 15px;
          line-height: 25px;
          font-weight: 500;
          letter-spacing: -0.75px;
          color: $gray-666;

          &::before {
            content: '';
            width: 3px;
            height: 3px;
            background-color: $gray-666;
            position: absolute;
            left: 10px;
            top: 10px;
          }
        }

        dd {
          width: calc(100% - 100px);
          line-height: 25px;
          font-size: 17px;
          font-weight: 500;
          letter-spacing: -0.85px;
          color: $gray-444;
        }
      }

      &.num {
        .row {
          dt {
            width: 40px;
            padding-left: 10px;

            &::before {
              content: none;
            }
          }

          dd {
            width: calc(100% - 40px);
          }
        }
      }
    }

    // 건립취지
    .history-content {
      flex: 1;
      display: flex;
      flex-direction: column;
      gap: 60px;

      .h-item {
        .h-item-title {
          font-size: 24px;
          font-weight: bold;
          margin-bottom: 10px;
        }

        .h-item-desc {
          font-size: 17px;
          letter-spacing: -0.85px;
          color: $gray-444;
        }

        .h-item-mark {
          margin-top: 30px;
        }
      }
    }

    // 위치지도
    .map-area {
      width: 100%;
      border-radius: 10px;
      border: 1px solid $gray-ccc;
      overflow: hidden;

      .map {
        width: 100%;
        aspect-ratio: 880 / 360;
      }

      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    // 인물 상세정보
    .featured-result {
      margin-top: 20px;
      margin-bottom: 30px;
      padding: 30px;

      .featured-result-inner {
        gap: 40px;
        justify-content: flex-start;
      }

      .featured-result-media {
        .img {
          width: 250px;
        }
      }

      .featured-result-content {
        padding: 0;

        .featured-info {
          .title {
            font-size: 32px;
          }
        }

        .featured-meta {
          display: flex;
          flex-wrap: wrap;
          gap: 10px 40px;
          margin-bottom: 0;

          .row {
            width: calc(50% - 20px);
            gap: 20px;

            .term {
              width: 118px;
              padding-left: 10px;

              &::before {
                left: 0;
              }
            }

            &.row-full {
              width: 100%;
            }
          }
        }
      }
    }

    .featured-info-box {
      padding: 0 27px;
      border: 1px solid #E3E7EE;
      border-radius: 10px;

      .featured-info {
        padding: 30px 0;
        display: flex;

        + .featured-info {
          border-top: 1px solid #e3e7ee;
        }

        .title {
          width: 225px;
          gap: 20px;
          flex-shrink: 0;
          font-size: 24px;
          font-weight: bold;
        }

        .info {
          flex: 1 0 0;
        }

        .meta {
          display: flex;
          flex-direction: column;
          gap: 13px;

          .row {
            display: flex;
            border-bottom: 1px dashed #e3e7ee;
            padding-bottom: 13px;

            &:last-child {
              border-bottom: 0;
            }

            .term {
              width: 100px;
              font-size: 17px;
              font-weight: 500;
              color: $gray-666;
              letter-spacing: -0.68px;
              flex-shrink: 0;
            }

            .desc {
              min-width: 0;
              flex: 1;
              font-size: 17px;
              letter-spacing: -0.68px;
              color: $gray-444;
            }
          }
        }
      }
    }

    .section-content {
      + .section-content {
        margin-top: 60px;
      }
    }

    // 사진 리스트
    .photo-list {
      grid-template-columns: repeat(5, minmax(0, 1fr));
      gap: 30px 44px;
      padding-bottom: 0;
    }
  }

  .relics-view-wrapper {
    padding: 40px 40px 50px;
    border: 1px solid #cadaee;
    border-radius: 20px;
    background-color: #f6f8f7;
    margin-bottom: 40px;
    margin-top: 20px;

    .img-viewer-wrapper {
      display: flex;
      justify-content: center;
      margin-bottom: 15px;
    }

    .img-viewer {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 717px;
      height: 450px;

      img {
        max-height: 100%;
        max-width: 100%;
        object-fit: contain;
      }
    }
  }

  .history-view-wrapper {
    padding: 40px;
    border: 1px solid #cadaee;
    border-radius: 20px;
    background-color: #f6f8f7;
    margin-bottom: 40px;
    margin-top: 20px;

    .img-viewer-wrapper {
      display: flex;
      gap: 30px;
      align-items: center;
      //justify-content: center;
    }

    .img-viewer {
      display: flex;
      justify-content: center;
      align-items: center;
      //width: 100%;
      //max-width: 570px;
      width: 260px;
      height: 180px;
      border: 1px solid $gray-ccc;
      background-color: #D9D9D9;

      img {
        display: block;
        max-width: 100%;
        max-height: 100%;
        width: auto;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  .history-wrapper {
    .tab-container {
      .tabs-box {
        .tabs {
          justify-content: center;

          .item {
            flex: none;
            width: 220px;

            &::after {
              background-color: #003675;
            }

            &.on {
              .tab-button {
                color: #003675;
              }
            }
          }
        }
      }
    }
  }
}

.popup-map-result {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 40px;

  .slider-wrapper {
    position: relative;
    width: 100%;
    overflow: hidden;
    padding: 10px 20px;
    border-radius: 20px;
    border: solid 1px #cadaee;
    background-color: #f6f8f7;

    .btn-circle-prev, .btn-circle-next {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 2;
      width: 38px;
      height: 38px;

      &.swiper-button-disabled {
        display: none;
      }
    }

    .btn-circle-prev {
      left: 11px;
    }

    .btn-circle-next {
      right: 11px;
    }

    .swiper {
      width: 100%;
      padding: 10px;

      .swiper-wrapper {
        display: flex;
      }

      .swiper-slide {
        width: 25%;
        flex-shrink: 0;
      }
    }

    .thumbnail {
      position: relative;
      width: 100%;
      border-radius: 10px;
      border: solid 2px transparent;
      overflow: hidden;
      transition: .2s;
      aspect-ratio: 4 / 3;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;
      }

      .txt {
        position: absolute;
        width: 100%;
        text-align: center;
        padding: 5px 10px;
        font-size: 15px;
        left: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, .6);
        color: $white;
        z-index: 1;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      &:hover {
        box-shadow: 0 2px 10px 0 rgba(37, 110, 244, 0.5);
        border: solid 2px #256ef4;
        background-color: #d6d6d6;
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .search-detail-box {
    margin-bottom: 40px;
  }

  .archive-container {
    .section-header {
      position: relative;
      flex-direction: column;
      align-items: flex-start;

      .btn-more {
        position: absolute;
        right: 0;
        top: 0;
      }

      .section-filter {
        position: static;
        top: auto;
        left: auto;
        transform: none;
        margin-top: 20px;
        margin-bottom: 5px;
      }
    }

    .section-title {
      font-size: 26px;
    }

    .result-group {
      .history-thumb-list {
        grid-template-columns: repeat(3, minmax(0, 1fr));
      }

      .person-list {
        grid-template-columns: repeat(3, minmax(0, 1fr));
      }

      .video-list {
        grid-template-columns: repeat(3, minmax(0, 1fr));
        gap: 30px 20px;
      }

      .photo-list {
        grid-template-columns: repeat(3, minmax(0, 1fr));
        gap: 30px 20px;
      }
    }

    .detail-content {
      .memorial-detail-content {
        gap: 20px;

        .photo-list {
          grid-template-columns: repeat(2, minmax(0, 1fr));
          gap: 30px 20px;
        }
      }

      // 인물 상세정보
      .featured-result {
        .featured-result-inner {
          gap: 20px;
          flex-direction: column;
        }

        .featured-result-media {
          display: flex;
          justify-content: center;
        }
      }
    }

    .history-wrapper {
      .tab-container {
        .tabs-box {
          .tabs {
            .item {
              flex: 1;
              width: auto;
            }
          }
        }
      }
    }
  }

  .popup-map-result {
    .slider-wrapper {
      .swiper-slide {
        width: 25%;
      }
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .archive-search {
    &-inner {
      padding: 70px 15px 0;
    }

    &-feature {
      padding: 0 15px;

      .btn-nav {
        &.btn-nav-prev {
          left: 0;
        }

        &.btn-nav-next {
          right: 0;
        }
      }
    }

    &-title {
      font-size: 36px;
    }

    &-form {
      .search-form {
        .form-select, .form-input {
          font-size: 17px;
          padding: 0 15px;
        }
      }
    }
  }

  .archive-container {
    .detail-content {
      .memorial-detail-content {
        flex-direction: column;
        flex-wrap: wrap;
        gap: 40px;
      }

      .photo-slider-wrapper {
        .btn-nav {
          &.btn-nav-prev {
            left: 27px;
          }

          &.btn-nav-next {
            right: 27px;
          }
        }

        .photo-slider {
          padding: 30px 30px;

          .swiper-slide {
            &:only-child {
              width: 100% !important;

              .img {
                width: 100%;
              }
            }
          }
        }
      }

      // 기본정보
      .info-list-wrap {
        width: 100%;
        padding: 0;
      }

      // 건립취지
      .history-content {
        gap: 40px;
      }

      // 인물 상세정보
      .featured-result {
        padding: 30px 20px;

        .featured-result-media {
          .img {
            width: 100%;
            max-width: 250px;
          }
        }

        .featured-result-content {
          .featured-meta {
            flex-direction: column;
            gap: 10px;

            .row {
              width: 100%;
            }
          }
        }
      }

      .featured-info-box {
        padding: 0 20px;

        .featured-info {
          flex-direction: column;
          padding: 20px 0;
          gap: 20px;
        }
      }
    }

    .result-group {
      .history-thumb-list {
        grid-template-columns: repeat(2, minmax(0, 1fr));
        gap: 20px 15px;
      }

      .photo-list {
        grid-template-columns: repeat(2, minmax(0, 1fr));
        gap: 20px 15px;
      }

      .video-list {
        grid-template-columns: repeat(2, minmax(0, 1fr));
        gap: 20px 15px;
      }
    }

    .history-view-wrapper {
      padding: 20px;

      .img-viewer-wrapper {
        flex-direction: column;
        gap: 20px;
      }
    }
  }

  .popup-map-result {
    .slider-wrapper {
      padding: 10px;
    }
  }
}

@media (max-width: $breakpoint-small) {
  .archive-search {
    &-feature {
      .swiper-slide {
        padding: 20px;
        gap: 10px;
        flex-direction: column;

        .img {
          max-width: 160px;
          margin: 0 auto;
          width: 100%;
          height: auto;
          aspect-ratio: 160 / 90;
        }

        .info {
          padding: 0 10px;

          .info-title {
            font-size: 18px;
          }
        }
      }
    }
  }

  .archive-container {
    .result-group {
      .person-list {
        grid-template-columns: repeat(2, minmax(0, 1fr));
      }
    }

    .detail-content {
      // 인물 상세정보
      .featured-result {
        .featured-result-inner {
          .featured-result-content {
            .featured-meta {
              .row {
                //flex-direction: column;
                //gap: 5px;

                .term {
                  width: auto;
                }

                .desc {
                  padding-left: 10px;
                  font-size: 16px;
                }
              }
            }
          }
        }
      }

      .featured-info-box {
        .featured-info {
          .meta {
            .row {
              flex-direction: column;
              gap: 5px;

              .desc {
                font-size: 16px;
              }
            }
          }
        }

        .dot-list {
          gap: 10px;
        }
      }
    }
  }
}

// 지도검색
.map-search-container {
  padding-bottom: 8px;

  .map-container {
    position: relative;
    width: 100%;
    height: 800px;
    margin-top: 20px;
    overflow: hidden;
    box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.3);

    .map-area {
      width: 100%;
      height: 100%;
    }

    .map-search-result {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: calc(360px + 24px);
      padding-right: 24px;
      z-index: 2;
      transition: transform .25s ease, opacity .25s ease;
      will-change: transform;

      .ico-fold {
        transition: .25s;
      }

      &.is-folded {
        transform: translateX(calc(calc(-100% + 24px)));

        .ico-fold {
          transform: rotate(180deg);
        }
      }
    }

    .map-search {
      position: relative;
      height: 100%;
      background-color: #f9f9f9;
      box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.3);

      .btn-fold {
        position: absolute;
        right: -24px;
        top: 50%;
        transform: translateY(-50%);
        width: 24px;
        height: 50px;
        box-shadow: 0.7px 0.7px 4px 0 rgba(0, 0, 0, 0.3);
        background-color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 0 3px 3px 0;
      }
    }

    .map-search-form {
      background-color: $point;
      padding: 20px;

      .search-field {
        position: relative;
        margin-bottom: 10px;

        .input-search {
          width: 100%;
          height: 50px;
          box-shadow: 0 2px 3px 0 rgba(0, 54, 117, 0.3);
          background-color: $white;
          border-radius: 4px;
          border: 0;
          padding: 0 50px 0 15px;
          font-size: 17px;
          letter-spacing: -.43px;

          &::placeholder {
            color: $gray-666;
          }
        }

        .btn-search {
          position: absolute;
          right: 0;
          top: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 50px;
          height: 50px;
        }
      }

      .search-sort {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;

        .btn-sort {
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 5px;
          padding: 0 15px;
          border-radius: 4px;
          height: 30px;
          line-height: 30px;
          font-size: 15px;
          font-weight: bold;

          &.btn-sort-green {
            background-color: #eaf6ec;
            color: #287439;
          }

          &.btn-sort-blue {
            background-color: #e7f4fe;
            color: #0c6cb4;
          }

          &.btn-sort-red {
            background-color: #fdefec;
            color: #be2f13;
          }

          .ico {
            flex-shrink: 0;
          }
        }
      }
    }

    .result-head {
      display: flex;
      justify-content: space-between;
      padding: 9.5px 20px;
      border-bottom: 1px solid $gray-eee;
      background-color: $white;

      .result-count {
        font-size: 15px;
        letter-spacing: -.38px;
        color: $gray-666;

        strong {
          font-weight: 500;
          color: $point;
        }
      }
    }

    .result-list-wrap {
      max-height: 501px;
      overflow-y: auto;
    }

    .result-list {
      position: relative;
      z-index: 1;
      padding: 5px 10px 0 0;
      background-color: #f9f9f9;

      .result-item {
        position: relative;
        border-top: 1px solid $gray-eee;
        background-color: $white;

        .btn-pin {
          width: 15px;
          height: 20px;
          display: flex;
          justify-content: center;
          align-items: center;
          position: absolute;
          right: 20px;
          top: 50%;
          transform: translateY(-50%);
        }

        &.is-active, &:hover {
          &.result-item-green {
            a {
              background-color: #f8fef9;
            }
          }

          &.result-item-blue {
            a {
              background-color: #f4faff;
            }
          }

          &.result-item-red {
            a {
              background-color: #fef7f5;
            }
          }
        }

        a {
          display: block;
          padding: 14px 40px 14px 20px;
        }

        .result-title {
          font-size: 19px;
          font-weight: 500;
          letter-spacing: -0.48px;
          margin-bottom: 5px;

          .result-marker {
            display: inline-flex;
            margin-right: 5px;
          }
        }

        .result-address {
          font-size: 15px;
          line-height: 1.33;
          color: $gray-666;
        }

        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 10px;
          height: 100%;
        }

        &.result-item-green {
          &::before {
            background-color: #eaf6ec;
          }

          .result-marker {
            color: #008645;
          }
        }

        &.result-item-blue {
          &::before {
            background-color: #e7f4fe;
          }

          .result-marker {
            color: #2462d6;
          }
        }

        &.result-item-red {
          &::before {
            background-color: #fdefec;
          }

          .result-marker {
            color: #dc2347;
          }
        }

        &:last-child {
          border-bottom: 1px solid $gray-eee;
        }
      }
    }
  }
}

@media (max-width: 450px) {
  .map-search-container {
    .map-container {
      .map-search-result {
        width: 100%;
      }
    }
  }
}

// 보훈컨텐츠 비쥬얼 영역
.visual-archive-container {
  .visual {
    width: 1280px;
    height: 320px;
    background-size: 1280px 320px;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 130px;
    margin: 0 auto;

    .visual-title {
      font-size: 40px;
      font-weight: bold;
      letter-spacing: -2px;
    }

    .visual-desc {
      font-size: 19px;
      font-weight: 500;
      letter-spacing: -0.95px;
      color: $gray-444;
    }
  }

  .visual-video {
    width: 1280px;
    height: 320px;
    background-size: 1280px 320px;
    padding-left: 160px;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    margin: 0 auto;

    .video {
      width: 460px;
      height: 258px;
      border-radius: 10px;
      overflow: hidden;

      .video-player {
        display: block;
        width: 100%;
        height: 100%;
      }
    }

    .video-info {
      display: flex;
      width: 550px;
      flex-direction: column;
      padding-left: 40px;
    }

    .visual-title {
      font-size: 32px;
      font-weight: bold;
      line-height: 1.25;
      letter-spacing: -1.6px;
      margin-bottom: 9px;
    }

    .visual-meta {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      font-size: 17px;
      font-weight: 500;
      letter-spacing: -0.85px;
      color: $gray-666;

      .meta {
        position: relative;
        padding-right: 10px;
        margin-right: 10px;

        &::after {
          content: '';
          position: absolute;
          width: 4px;
          height: 4px;
          border-radius: 100%;
          right: -1px;
          top: 10px;
          background-color: $gray-999;
        }

        &:last-child {
          &::after {
            content: none;
          }
        }
      }
    }

    .visual-desc {
      font-size: 19px;
      line-height: 1.47;
      letter-spacing: -0.95px;
    }
  }

  .visual-slider {
    width: 1280px;
    height: 320px;
    background-size: 1280px 320px;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    margin: 0 auto;
    padding-left: 160px;

    .swiper {
      width: 460px;
      height: 258px;
      overflow: hidden;
      border-radius: 10px;
      box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2);
      border: solid 1px $gray-999;
      margin: 0;
      flex-shrink: 0;
      min-width: 0;

      .swiper-slide {
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }

    .visual-info {
      display: flex;
      flex-direction: column;
      width: 550px;
      padding-left: 40px;
    }

    .visual-title {
      font-size: 32px;
      font-weight: bold;
      line-height: 1.25;
      letter-spacing: -1.6px;
      margin-bottom: 5px;
      min-height: 80px;
      display: -webkit-box;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
    }

    .visual-desc {
      font-size: 19px;
      line-height: 1.47;
      letter-spacing: -0.95px;
      margin-bottom: 25px;
      min-height: 55.84px;
      display: -webkit-box;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
    }

    .slider-control {
      display: flex;
      align-items: center;

      .btn-nav {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
        min-width: 0;
        flex-shrink: 0;

        img {
          width: 100%;
          height: 100%;
        }

        + .btn-nav {
          margin-left: 5px;
        }
      }

      .page-num {
        height: 30px;
        line-height: 30px;
        padding: 0 20px;
        border-radius: 15px;
        width: auto;
        margin-left: 15px;
        background-color: rgba(0, 0, 0, .6);
        font-size: 15px;
        letter-spacing: -.75px;

        .current {
          color: $white;
          font-weight: bold;
        }

        .total {
          color: $gray-ccc;

          &::before {
            content: '/';
            margin: 0 4px;
          }
        }
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .visual-archive-container {
    .visual {
      width: 100%;
      height: auto;
      aspect-ratio: 1280 / 320;
      background-size: cover;
      padding-left: 10.16vw;

      .visual-title {
        font-size: 3.13vw;
        letter-spacing: -0.16vw;
      }

      .visual-desc {
        font-size: 1.48vw;
        letter-spacing: -0.07vw;
      }
    }

    .visual-video {
      width: 100.00vw;
      height: 25.00vw;
      background-size: 100.00vw 25.00vw;
      padding-left: 12.50vw;

      .video {
        width: 35.94vw;
        height: 20.16vw;
        border-radius: 0.78vw;
      }

      .video-info {
        width: 42.97vw;
        padding-left: 3.13vw;
      }

      .visual-title {
        font-size: 2.50vw;
        letter-spacing: -0.13vw;
        margin-bottom: 0.70vw;
      }

      .visual-meta {
        font-size: 1.33vw;
        letter-spacing: -0.07vw;

        .meta {
          padding-right: 0.78vw;
          margin-right: 0.78vw;

          &::after {
            width: 0.31vw;
            height: 0.31vw;
            right: -0.08vw;
            top: 0.78vw;
          }
        }
      }

      .visual-desc {
        font-size: 1.48vw;
        line-height: 1.47;
        letter-spacing: -0.07vw;
      }
    }

    .visual-slider {
      width: 100.00vw;
      height: 25.00vw;
      background-size: 100.00vw 25.00vw;
      padding-left: 12.50vw;

      .swiper {
        width: 35.94vw;
        height: 20.16vw;
        border-radius: 0.78vw;
        box-shadow: 0 0.16vw 0.39vw 0 rgba(0, 0, 0, 0.2);
        border: solid 0.08vw $gray-999;
      }

      .visual-info {
        width: 42.97vw;
        padding-left: 3.13vw;
      }

      .visual-title {
        font-size: 2.50vw;
        letter-spacing: -0.13vw;
        margin-bottom: 0.39vw;
        min-height: 6.25vw;
      }

      .visual-desc {
        font-size: 1.48vw;
        line-height: 1.47;
        letter-spacing: -0.07vw;
        margin-bottom: 1.95vw;
        min-height: 4.36vw;
      }

      .slider-control {
        .btn-nav {
          width: 2.34vw;
          height: 2.34vw;

          + .btn-nav {
            margin-left: 0.39vw;
          }
        }

        .page-num {
          height: 2.34vw;
          line-height: 2.34vw;
          padding: 0 1.56vw;
          border-radius: 1.17vw;
          margin-left: 1.17vw;
          font-size: 1.17vw;
          letter-spacing: -.5.86vw;

          .total {
            &::before {
              margin: 0 0.31vw;
            }
          }
        }
      }
    }
  }
}