/*
  이용안내
*/
@use "variables" as *;
@use "config" as *;

.user-guide-container {
  .result-group-head {
    margin-bottom: 20px;
  }

  .terms {
    font-size: 17px;

    .txt {
      margin-bottom: 40px;
    }

    .actions {
      display: flex;
      flex-wrap: wrap;
      gap: 20px;
      justify-content: center;
      margin-bottom: 60px;
    }
  }

  .view-detail {
    border-top: 1px solid $gray-666;

    .view-head {
      background-color: #f9f9f9;
      border-bottom: 1px solid $gray-eee;
      padding: 15px 20px;

      .view-meta {
        display: flex;
        flex-direction: column;
        gap: 5px;
      }

      .view-title {
        font-size: 19px;
        letter-spacing: -0.76px;
      }

      .view-summary {
        display: flex;
        flex-wrap: wrap;
        gap: 5px 0;

        .summary-item {
          display: flex;
          align-items: center;
          flex-wrap: wrap;
          gap: 8px;

          + .summary-item {
            &::before {
              content: '|';
              margin: 0 7px 0 15px;
              color: $gray-ccc;
            }
          }

          dt {
            font-size: 17px;
            font-weight: bold;
            letter-spacing: -0.68px;
            color: $gray-444;
          }

          dd {
            font-size: 17px;
            letter-spacing: -0.68px;
            color: $gray-666;
          }
        }
      }
    }

    .view-body {
      padding: 30px 20px;
    }

    .post-nav {
      border-top: 1px solid $gray-eee;

      .post-nav-item {
        display: flex;
        border-bottom: 1px solid $gray-eee;
      }

      .nav-label {
        display: flex;
        align-items: center;
        font-weight: 500;
        gap: 5px;
        padding: 20px;
      }

      .nav-link {
        padding: 20px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;

        &:hover {
          text-decoration: underline;
          text-underline-offset: 4px;
        }
      }
    }
  }

  .intro-banner {
    height: 280px;
    background-size: cover;
    background-repeat: no-repeat;
    background-image: url(../images/userGuide/bg-heading.png);
    margin-bottom: 60px;

    &.user-guide {
      background-image: url(../images/userGuide/bg-ug-heading.png);
      color: $white;
    }

    .inner {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      height: 100%;
    }

    .sub {
      display: inline-flex;
      width: fit-content;
      font-size: 17px;
      letter-spacing: -0.68px;
      font-weight: 500;
      padding: 2px 12px;
      color: $white;
      border-radius: 6px;
      background-color: rgba(0, 54, 117, 0.7);
      margin-bottom: 5px;
      line-height: 26px;
    }

    .title {
      font-size: 32px;
      font-weight: bold;
      letter-spacing: -1.28px;
      margin-bottom: 20px;
    }

    .desc {
      font-size: 17px;
      line-height: 1.5;
      letter-spacing: -0.68px;
    }
  }

  .intro-view {
    display: flex;
    gap: 60px;
    padding: 30px;
    border-radius: 20px;
    border: solid 1px #ddebf5;
    background-color: #f3f9fe;

    .chapter-nav {
      width: 240px;
      padding: 10px;
      border-radius: 10px;
      border: solid 1px #a2afc7;
      background-color: $white;
      flex-shrink: 0;
      min-width: 0;

      .chapter-list {
        display: flex;
        flex-direction: column;

        .chapter-item {
          position: relative;
          border-top: 1px solid $gray-eee;

          &:first-child {
            border-top: 0;
          }

          &.is-active {
            border-top: 0;

            .chapter-link {
              background-color: #2462d6;
            }

            .chapter-label {
              color: $white;
              border-color: $white;
            }

            .chapter-title {
              color: $white;
            }

            &::after {
              content: '';
              position: absolute;
              bottom: -1px;
              left: 0;
              width: 100%;
              height: 1px;
              background-color: $white;
              z-index: 1;
            }
          }

          .chapter-link {
            display: flex;
            flex-direction: column;
            gap: 5px;
            padding: 20px;
            border-radius: 6px;
            min-height: 120px;
          }

          .chapter-label {
            display: inline-flex;
            width: fit-content;
            padding: 0 10px;
            height: 24px;
            line-height: 22px;
            border-radius: 61px;
            border: 1px solid $gray-666;
            font-size: 15px;
            letter-spacing: -0.6px;
            color: $gray-666;
          }

          .chapter-title {
            font-size: 24px;
            font-weight: bold;
            letter-spacing: -0.96px;
            color: $gray-444;
          }
        }
      }
    }

    .chapter-panel {
      display: none;

      &.is-active {
        display: block;
      }
    }

    .media-content {
      flex: 1;
    }

    .media-header {
      padding-top: 15px;
      text-align: center;
      margin-bottom: 30px;

      .media-title {
        font-size: 32px;
        font-weight: bold;
        letter-spacing: -1.28px;
      }

      .media-desc {
        font-size: 19px;
        letter-spacing: -0.76px;
      }
    }

    .media-video {
      .video {
        width: 100%;
        aspect-ratio: 890 / 500;

        iframe {
          width: 100%;
          height: 100%;
        }
      }
    }
  }

  .user-guide {
    display: flex;
    flex-direction: column;
    gap: 60px;

    .content-title {
      font-size: 24px;
      font-weight: bold;
      letter-spacing: -0.96px;
      margin-bottom: 20px;
    }
  }

  .browser-list {
    display: grid;
    grid-template-columns: repeat(5, minmax(0, 1fr));
    gap: 20px 45px;

    .browser-item {
      display: flex;
      height: 200px;
      flex-direction: column;
      justify-content: flex-end;
      align-items: center;
      padding: 15px;
      border-radius: 10px;
      border: solid 1px #ddebf5;
      background-color: #f3f9fe;

      img {
        display: block;
        width: 100px;
        height: 100px;
        margin: 0 auto 30px;
      }

      .txt {
        font-size: 20px;
        letter-spacing: -.8px;
        color: $gray-000;
      }
    }
  }

  .info-gray {
    padding: 15px;
    background-color: #f9f9f9;
    border-radius: 6px;
    
    .txt {
      display: block;
      text-align: center;
      font-size: 19px;
      letter-spacing: -0.76px;
    }
  }

  .info-list {
    .row {
      display: flex;
      border-top: 1px dashed #e3e7ee;
      padding: 7px 0;

      &:last-child {
        border-bottom: 1px dashed #e3e7ee;
      }

      dt {
        position: relative;
        width: 115px;
        padding-left: 20px;
        font-size: 15px;
        line-height: 25px;
        font-weight: 500;
        letter-spacing: -0.75px;
        color: $gray-666;

        &::before {
          content: '';
          width: 3px;
          height: 3px;
          background-color: $gray-666;
          position: absolute;
          left: 10px;
          top: 10px;
        }
      }

      dd {
        width: calc(100% - 115px);
        line-height: 25px;
        font-size: 17px;
        font-weight: 500;
        letter-spacing: -0.85px;
        color: $gray-444;
      }
    }

    &.num {
      .row {
        dt {
          width: 40px;
          padding-left: 10px;

          &::before {
            content: none;
          }
        }

        dd {
          width: calc(100% - 40px);
        }
      }
    }
  }

  .location-guide-wrap {
    .map-wrap {
      aspect-ratio: 1280 / 500;
      border-radius: 10px;
      overflow: hidden;
      margin-bottom: 20px;
      border: 1px solid $gray-ccc;

      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  .map-info-list {
    display: flex;
    border-radius: 10px;
    border: solid 1px #ddebf5;
    background-color: #f3f9fe;
    padding: 15px;

    .info-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 10px;
      flex: 1;
      padding: 0 15px;
      text-align: center;

      + .info-item {
        border-left: 1px dashed #a2afc7;
      }

      .title {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 5px;

        .txt {
          font-size: 17px;
          line-height: 24px;
          font-weight: bold;
          letter-spacing: -0.68px;
          color: $point;
        }
      }

      .value {
        font-size: 17px;
        letter-spacing: -0.68px;
      }
    }
  }

  .content-section {
    .content-title {
      font-size: 24px;
      font-weight: bold;
      letter-spacing: -0.96px;
      margin-bottom: 20px;
    }

    .content-desc {
      font-size: 17px;
      letter-spacing: -0.68px;
      color: $gray-444;

      b {
        color: $gray-222;
      }
    }

    + .content-section {
      margin-top: 60px;
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .user-guide-container {
    .view-detail {
      .view-head {
        .view-summary {
          .summary-item {
            dt {
              font-size: 15px;
            }

            dd {
              font-size: 15px;

              &::before {
                margin: 0 5px;
              }
            }
          }
        }
      }
    }

    .intro-view {
      flex-direction: column;
      gap: 30px;
      padding: 20px;

      .chapter-nav {
        width: 100%;

        .chapter-list {
          //flex-direction: row;
          //flex-wrap: wrap;

          .chapter-item {
            flex: 1;
            //border-top: 0;

            .chapter-link {
              flex-direction: row;
              align-items: center;
              padding: 10px;
              min-height: auto;
              gap: 10px;
            }

            .chapter-label {
              font-size: 13px;
              white-space: nowrap;
            }

            .chapter-title {
              font-size: 20px;
            }
          }
        }
      }

      .media-header {
        padding-top: 0;

        .media-title {
          font-size: 24px;
          margin-bottom: 10px;
        }

        .media-desc {
          font-size: 15px;
        }
      }
    }

    .browser-list {
      display: grid;
      grid-template-columns: repeat(5, minmax(0, 1fr));
      gap: 20px;

      .browser-item {
        img {
          width: 80px;
          height: 80px;
        }
      }
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .user-guide-container {
    .intro-banner {
      padding: 30px 0;
      height: auto;
      margin-bottom: 40px;

      .title {
        font-size: 26px;
      }

      .sub, .desc {
        font-size: 16px;

        br {
          display: none;
        }
      }
    }

    .browser-list {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
      gap: 20px;

      .browser-item {
        height: 150px;

        img {
          width: 70px;
          height: 70px;
          margin: 0 auto 15px;
        }

        .txt {
          font-size: 17px;
        }
      }
    }

    .info-gray {
      .txt {
        font-size: 17px;
      }
    }

    .user-guide {
      gap: 40px;
    }

    .location-guide-wrap {
      .map-wrap {
        aspect-ratio: auto;
        height: 500px;
      }
    }

    .map-info-list {
      flex-direction: column;
      padding: 0 15px;

      .info-item {
        padding: 15px;

        + .info-item {
          border-left: 0;
          border-top: 1px dashed #a2afc7;
        }
      }
    }
  }
}